// import Swiper from 'swiper';
// import 'swiper/css';

const swiper = new Swiper('.slider-products', {
    speed: 400,
    slidesPerView: 1,
    spaceBetween: 30,
    loop: true,
    navigation: {
      nextEl: ".swiper-button-next",
      prevEl: ".swiper-button-prev",
    },
    pagination: {
      el: ".slider-products__pagination",
      clickable: true,
    },
    breakpoints: {
      // when window width is >= 320px
      320: {
        slidesPerView: 2,
        spaceBetween: 20
      },
      // when window width is >= 480px
      480: {
        slidesPerView: 3,
        spaceBetween: 30
      },
      // when window width is >= 640px
      640: {
        slidesPerView: 4,
        spaceBetween: 40
      }
    }
  });
const reviewsSlider = new Swiper('.reviewsSlider', {
    speed: 400,
    slidesPerView: 1,
    // spaceBetween: 30,
    loop: true,
    pagination: {
      el: ".slider-reviews__pagination",
      clickable: true,
      navigation: {
        nextEl: ".swiper-button-next",
        prevEl: ".swiper-button-prev",
      },
    },
  });