const modalRegicter = document.querySelector('.modal');
const btnSecond = document.querySelectorAll('.btn-second');
const modalClose = document.querySelector('.modal-close');

modalRegicter.addEventListener('click',(e) => {
    if(e.target.className === 'modal modal-open') {
        modalRegicter.classList.remove('modal-open')
    }
})

btnSecond.forEach(item => {
    item.addEventListener('click', (e) => {
        if(e.target) {
            modalRegicter.classList.add('modal-open')
        }
    })
})

modalClose.addEventListener('click', (e) => {
    modalRegicter.classList.remove('modal-open');
})
